import { Pipe, PipeTransform } from '@angular/core';
import { Client } from '../_models/client';


@Pipe({
  name: 'seachByClient'
})
export class SeachByClientPipe implements PipeTransform {

  transform(clients: Client[], searchText: string) {
    if (clients) {
      return clients.filter(client => client.name.indexOf(searchText) !== -1);
    }
    return '';
  }

}



