import { Component, OnInit, ViewChild } from '@angular/core';
import { Client } from '../../_models/client';
import { ClientService } from '../../_services/client.service';
import { AlertifyService } from '../../_services/alertify.service';
import { ActivatedRoute, Router } from '@angular/router';
import { NgForm, FormGroup, FormControl, Validators, FormBuilder } from '@angular/forms';
import { Payment } from '../../_models/payment';
import { getDate } from 'ngx-bootstrap/chronos/utils/date-getters';
import { PaymentList } from '../../_models/paymentList';
import { DeleteComponent } from '../../../app/delete-modal/delete-modal';

@Component({
  selector: 'app-clientes-detail',
  templateUrl: './clientes-detail.component.html',
  styleUrls: ['./clientes-detail.component.css']
})
export class ClientesDetailComponent implements OnInit {
client: Client;
model: Client;
modelPay: Payment;
payForm: FormGroup;
currentDate = new Date();
dateString: string;
editEnable = false ;
paymentList: PaymentList[];
@ViewChild('clientForm') clientForm: NgForm;

  constructor(private clientService: ClientService, private alertify: AlertifyService,
  private route: ActivatedRoute, private router: Router, private fb: FormBuilder) { }

  ngOnInit() {
    this.dateString = this.currentDate.getFullYear() + '-' + ('0' + (this.currentDate.getMonth() + 1)).slice(-2)
    + '-' + ('0' + this.currentDate.getDate()).slice(-2);
    this.route.data.subscribe(data => {
      this.client = data['client'];
      this.model = data['client'];
    });
    this.getPayments(this.model.id);
    this.createPayForm();
    // this.payForm = new FormGroup({
    //   payValue: new FormControl(0.00, [Validators.required, Validators.pattern('^[0-9]+(\.[0-9]{1,2})?$')] ),
    //   notes: new FormControl('')
    // });
    this.getTotal();
  }

  createPayForm() {
    this.payForm = this.fb.group(
      {
        TS: [this.dateString, Validators.required],
        Value: ['', [Validators.required, Validators.pattern('^[0-9]+(\.[0-9]{1,2})?$')]],
        Notes: [''],
        Method: [null],
        Quantity: [1],
        ClientForeignKey: [0]
      }
    );
  }
  editClient() {
    this.clientService.updateClient(this.client.id, this.client).subscribe(next => {
      this.alertify.success('Cliente alterado com sucesso');
      this.clientForm.reset(this.client);
      this.editEnable = !this.editEnable;
    }, error => {
      this.alertify.error(error);
    });
  }

  enableEdit() {
    this.clientService.getClient(this.client.id).subscribe((client: Client) => {
      this.clientForm.reset(client);
    });
    this.editEnable = !this.editEnable;
  }

  deleteClient() {
    this.clientService.deleteClient(this.client).subscribe(next => {
      this.alertify.success('Cliente removido com sucesso');
      this.router.navigate(['/clientes']);
    }, error => {
      this.alertify.error(error);
    });
  }

  addPay(method: string) {
    if (this.payForm.valid) {
    this.modelPay = Object.assign({}, this.payForm.value);
    this.modelPay.Method = method;
    if (this.modelPay.Notes == null) {
      this.modelPay.Notes = '';
    }
    this.modelPay.ClientForeignKey = this.client.id;
    this.clientService.addPayment(this.modelPay).subscribe(next => {
      this.alertify.success('Registro salvo com sucesso!');
      this.payForm.reset();
      this.payForm.get('Quantity').setValue(1);
      this.getPayments(this.model.id);
      this.payForm.get('TS').setValue(this.dateString);
    }, error => {
      this.alertify.error(error);
    });
  }
  }

  getPayments(clientId: number) {
    this.clientService.getPayments(clientId).subscribe((payList: PaymentList[]) => {
      this.paymentList = payList;
    }, error => {
      this.alertify.error(error);
    });
  }

  getTotal() {
    if (this.paymentList != null) {
      let total = 0;
      for (let i = 0; i < this.paymentList.length; i++) {
          const product = this.paymentList[i];
          if (product.method === 'Comprou') {
            total += product.value * product.quantity;
          }
          if (product.method === 'Pagou') {
            total -= product.value * product.quantity;
          }
          return total;
      }
      return 0;
    }
  }

  getTotalBuy() {
    if (this.paymentList != null) {
      let total = 0;
      for (let i = 0; i < this.paymentList.length; i++) {
          const product = this.paymentList[i];
          if (product.method === 'Comprou') {
          total += product.value * product.quantity;
          }
      }
      return total;
    }
    return 0;
  }

  getTotalSell() {
    if (this.paymentList != null) {
      let total = 0;
      for (let i = 0; i < this.paymentList.length; i++) {
          const product = this.paymentList[i];
          if (product.method === 'Pagou') {
          total += product.value * product.quantity;
          }
      }
      return total;
    }
    return 0;
}

  deletePayment(p: Payment) {
    if (window.confirm('Deseja mesmo remover ?')) {
      this.clientService.deletePayment(p).subscribe(next => {
        this.alertify.success('Registro removido com sucesso');
        this.getPayments(this.model.id);
      }, error => {
        this.alertify.error(error);
      });
    }
  }
}



