import { HttpModule } from '@angular/http';
import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { BsDropdownModule } from 'ngx-bootstrap';

import { AppComponent } from './app.component';
import { LoginComponent } from './login/login.component';
import { NavComponent } from './nav/nav.component';
import { AuthService } from './_services/auth.service';
import { HomeComponent } from './home/home.component';
import { RegisterComponent } from './register/register.component';
import { AlertifyService } from './_services/alertify.service';
import { ClientesComponent } from './clientes/clientes.component';
import { VendasComponent } from './vendas/vendas.component';
import { RouterModule } from '@angular/router';
import { appRoutes } from './route';
import { AuthGuard } from './_guards/auth.guard';
import { ClientService } from './_services/client.service';
import { SeachByClientPipe } from './_pipes/seachByClient.pipe';
import { ClientesDetailComponent } from './clientes/clientes-detail/clientes-detail.component';
import { ClientDetailResolver } from './_resolvers/clients-detail-resolver';
import { SetDecimalPipe } from './_pipes/setDecimal.pipe';
import { JwtModule } from '@auth0/angular-jwt';
import { HttpClientModule } from '@angular/common/http';
import { ErrorInterceptorProvider } from './_services/error.interceptor';
import {NgbModule} from '@ng-bootstrap/ng-bootstrap';
import { DeleteComponent } from './delete-modal/delete-modal';

export function getAccessToken(): string {
  return localStorage.getItem('token');
}

export const jwtConfig = {
  tokenGetter: getAccessToken,
  whitelistedDomains: ['localhost:5000']
};

@NgModule({
  declarations: [
    AppComponent,
    LoginComponent,
    NavComponent,
    HomeComponent,
    RegisterComponent,
    ClientesComponent,
    VendasComponent,
    SeachByClientPipe,
    SetDecimalPipe,
    ClientesDetailComponent,
    DeleteComponent
],
  imports: [
    BrowserModule,
    HttpModule,
    FormsModule,
    ReactiveFormsModule,
    BsDropdownModule.forRoot(),
    RouterModule.forRoot(appRoutes),
    HttpClientModule,
    NgbModule.forRoot(),
    JwtModule.forRoot({
        config: jwtConfig
    })
  ],
  providers: [
    AuthService,
    AlertifyService,
    AuthGuard,
    ClientService,
    ClientDetailResolver,
    ErrorInterceptorProvider
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }



