
import { Routes } from '@angular/router';
import { HomeComponent } from './home/home.component';
import { VendasComponent } from './vendas/vendas.component';
import { ClientesComponent } from './clientes/clientes.component';
import { AuthGuard } from './_guards/auth.guard';
import { ClientesDetailComponent } from './clientes/clientes-detail/clientes-detail.component';
import { ClientDetailResolver } from './_resolvers/clients-detail-resolver';

export const appRoutes: Routes = [
{ path: 'home', component: HomeComponent },
{
    path: '',
    runGuardsAndResolvers: 'always',
    canActivate: [AuthGuard],
    children: [
        { path: 'vendas', component: VendasComponent },
        { path: 'clientes', component: ClientesComponent },
        { path: 'clientes/:id', component: ClientesDetailComponent, resolve: {client: ClientDetailResolver, model: ClientDetailResolver}}
    ]
},
{ path: '**', redirectTo: 'home', pathMatch: 'full' }

];



