import { Injectable } from '@angular/core';
import { ActivatedRouteSnapshot, Resolve, Router, RouterStateSnapshot } from '@angular/router';
import { Client } from '../_models/client';
import { ClientService } from '../_services/client.service';
import { AlertifyService } from '../_services/alertify.service';
import 'rxjs/add/operator/catch';
import 'rxjs/add/observable/of';
import { Observable } from 'rxjs/Observable';

@Injectable()
export class ClientDetailResolver implements Resolve<Client> {
    constructor(private clientService: ClientService, private router: Router, private alertify: AlertifyService) {}

    resolve(route: ActivatedRouteSnapshot): Observable<Client> {
        return this.clientService.getClient(route.params['id']).catch(error => {
            this.alertify.error('Erro ao retornar dados');
            this.router.navigate(['/clients']);
            return Observable.of(null);
        });
    }
}



