import { Component, OnInit } from '@angular/core';
import { ClientService } from '../_services/client.service';
import { BI } from '../_models/bi';
import { AlertifyService } from '../_services/alertify.service';

@Component({
  selector: 'app-vendas',
  templateUrl: './vendas.component.html',
  styleUrls: ['./vendas.component.css']
})
export class VendasComponent implements OnInit {
  biTotals: BI[];
  totalBuy = 0;
  totalPay = 0;
  constructor(private clientService: ClientService, private alertify: AlertifyService) { }

  ngOnInit() {
    this.loadTotals();
  }

  loadTotals() {
    this.clientService.getBITotals().subscribe((biTotals: BI[]) => {
      biTotals.forEach((b) => {
        if (b.method === 'Comprou') {
          this.totalBuy += b.value;
          console.log(this.totalBuy);
        } else {
          this.totalPay += b.value;
          console.log(this.totalPay);
        }
      });
      this.biTotals = biTotals;
    }, error => {
      this.alertify.error(error);
    });
  }

}



