import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'setDecimal'
})
export class SetDecimalPipe implements PipeTransform {
  factor: any;
  transform(input: number, places: number, args?: any): any {
    if (isNaN(input)) {
      return input;
    }
    this.factor = '1' + Array(+(places > 0 && places + 1)).join('0');
        return Math.round(input * this.factor) / this.factor;
  }

}



