import { Injectable } from '@angular/core';
import { environment } from '../../environments/environment';
import { Http, RequestOptions, Headers } from '@angular/http';
import { Observable } from 'rxjs/Observable';
import { Client } from '../_models/client';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';
import 'rxjs/add/observable/throw';
import { Payment } from '../_models/payment';
import { PaymentList } from '../_models/paymentList';
import { HttpClient } from '@angular/common/http';
import { BI } from '../_models/bi';

@Injectable()
export class ClientService {
    baseUrl = environment.apiUrl;
constructor(private authHttp: HttpClient) { }

getClients(): Observable<Client[]> {
    return this.authHttp.get<Client[]>(this.baseUrl + 'clients');
}

getClient(id): Observable<Client> {
    return this.authHttp.get<Client>(this.baseUrl + 'clients/' + id);
}

updateClient(id: number, client: Client) {
    return this.authHttp.put(this.baseUrl + 'clients/' + id, client);
}

registerClient(model: Client) {
    return this.authHttp.post(this.baseUrl + 'clients/registerClient', model);
}

deleteClient(model: Client) {
    return this.authHttp.post(this.baseUrl + 'clients/deleteClient', model);

}

addPayment(model: Payment) {
    return this.authHttp.post(this.baseUrl + 'clients/addPayment', model);
}

getPayments(clientId: number): Observable<PaymentList[]> {
    return this.authHttp.get<PaymentList[]>(this.baseUrl + 'clients/getPayments/' + clientId );
}

deletePayment(model: Payment) {
    return this.authHttp.post(this.baseUrl + 'clients/deletePayment/', model);
}

getBITotals(): Observable<BI[]> {
    return this.authHttp.get<BI[]>(this.baseUrl + 'clients/getBITotals');
}
}



